.hero-img{
    width:100%;
    background:rgba(0,0,0,.5);
    height:60vh;
}

.heading{
    width:100%;
    height:100%;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.hero-img::before{
    content:"";
    background:url('https://images.unsplash.com/photo-1526498460520-4c246339dccb?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=2070&q=80');
    background-size: cover;
    background-position: center top;
    position:absolute;
    top:0;
    left:0;
    z-index: -1;
    height: 60%;
    width: 100%;
}

.hero-img h1{
    font-size: 2.4rem;
}
.hero-img p{
    font-size: 1.4rem;
    text-align: center;
}

@media screen and (max-width:690px){
    .hero-img h1{
        font-size: 2rem;
    }
}