@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;400;700&family=Work+Sans:wght@100;300;400;500;700&display=swap');



*{
  box-sizing: border-box;
  margin:0;
  padding:0;
  font-family: 'Outfit',sans-serif;
}

body{
  background: #f1f1f1;;
}
.project-heading{
  color:#000;
}
h1,h4,p,a{
  color:#fff;
  text-decoration: none;
}
ul{
  list-style-type: none;
}

.btn{
  padding:12px 32px;
  font-size: 1rem;
  text-transform:uppercase;
  background: rgb(254, 68, 27);
  color:#000;
  border:1px solid #fff;
  font-weight:400;
  cursor: pointer;
}

.btn-light{
  background-color: transparent;
  color:#fff;
  border:1px solid #fff;
  padding:12px 32px;
  font-size: 1rem;
  text-transform:uppercase;
  font-weight:400;
  cursor: pointer;

}

.btn:hover{
  background-color: rgb(181, 1, 1);
  color:#080808;
  transition:0.3s;
}
.btn-light:hover{
  background: rgb(189, 35, 0);
  color:#000;
  transition:0.3s;
}