.footer{
    width: 100%;
    padding:6rem 0;
    background-color: rgb(0, 0, 0);
}

.footer-container{
    max-width: 1140px;
    margin:auto;
    display:grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap:40px;
}

.left{
    height:100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding:1rem;
}

.location{
    display:flex;
    margin-bottom: .8rem;
}

.location p{
    line-height: 30px;
}
.right{
    height:100%;
     max-width: 100%;
     display:flex;
     flex-direction: column;
     padding:1rem;
}

.social{
    margin-top:1rem;
}

h4{
    font-size: 1.2rem;
    padding-bottom: .7rem;
}
p{
    font-size: 1.2rem;
    margin-top:-5px;
}

@media screen and (max-width:690px){
    .footer-container{
        grid-template-columns: 1fr;
    }
}

.copyright{
    /* 
     */
        text-align: center;
        margin-top:30px;
        
    }
.copyright p{
    font-size:12px;
    color:rgb(220, 220, 221)
}